.girl-card{
  position: relative;
  padding-right: 1.44em;
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__name{
    position: absolute;
    display: flex;
    align-items: center;
    height: 3.125em;
    right: 0;
    bottom: 4.81em;
    padding: 0 1.5em;
    color: #3E475B;
    font-family: $font2;
    background-color: #fff;
    span{
      font-size: 2.375em;
      letter-spacing: 0.05em;
    }
  }
}


@media screen and (max-width: 576px) {
  .girl-card {
    &__name {
      height: 2.1875em;
      span {
        font-size: 1.1875em;
      }
    }
  }
}
