.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-red{
    text-transform: uppercase;
    color: #fff;
    background-color: $primary;
    span{
        font-size: .875em;
    }
    &:hover{
        color: #fff;
        background-color: darken($primary,10%);;
    }
}
