.main,
.info{
  &__lines{
    position: absolute;
    svg{
      width: 100%;
      height: auto;
    }
    ._view2{
      display: none;
    }
  }
}

.main{
  padding: 2.87em 3.91em 3em 10.37em;
  color: #fff;
  position: relative;
  overflow: hidden;
  &__logo{
    width: 26.6875em;
    max-width: 100%;
    img{
      width: 100%;
      display: block;
    }
  }
  &__inner{
    margin-top: 8.75em;
    display: grid;
    grid-template-columns: calc(100% - 59em) 52em;
    gap: 7em;
  }
  &__content{
    padding-left: 8.34em;
  }
  &__img{
    margin-top: -16.5em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    font-size: 3.75em;
    line-height: 142%;
    font-weight: 700;
  }
  &__text{
    margin-top: 1.625em;
    p{
      font-size: 2.375em;
      line-height: 136%;
    }
    a{
      text-decoration: underline;
    }
    b{
      font-weight: 700;
    }
    a{
      text-decoration: underline;
    }
  }
  &__lines{
    left: .5em;
    width: 117.25em;
    top: 0;
    opacity: 0;
  }
  &._active{
    .main__lines{
      opacity: 1;
    }
  }
}

._line{
  transition: all 1.4s;
}

.info{
  position: relative;
  overflow: hidden;
  padding: 4.83em 11.81em 10em 11.81em;
  display: grid;
  grid-template-columns: 39.5em calc(100% - 45.5em);
  justify-content: space-between;
  &__content{
    max-width: 42.3em;
  }
  &__stat-title,
  &__text p,
  &__title{
    font-size: 1.875em;
    line-height: 142%;
  }
  &__title{
    font-weight: 500;
  }
  &__text{
    margin-top: 6em;
    font-weight: 300;
  }
  &__stat{
    margin-top: 5em;
    transform: translateX(11.4em);
    &-title{
      font-weight: 300;
    }
    &-num{
      color: #E51D3E;
      font-size: 3.4375em;
      line-height: 107%;
      font-weight: 700;
    }
  }
  &__lines{
    bottom: 0;
    left: 0;
    width: 100%;
  }
  &__line2,
  &__line3{
    display: none;
  }
}

.info-slider{
  position: relative;
  padding-right: 3.75em;
  .swiper{
    &-container{
      position: relative;
      z-index: 2;
    }
    &-slide{
      opacity: 0;
      &-active{
        opacity: 1;
      }
    }
  }
  &__bg{
    position: absolute;
    top: 3.63em;
    right: 0;
    width: 34.1875em;
  }
}

.stat{
  padding: 10em 1.75em 7em;
  position: relative;
  &__lines,
  &__lines2{
    pointer-events: none;
    position: absolute;
    width: 100%;
    height: auto;
    left: 0;
    top: -4em;
  }
  &__lines2{
    display: none;
  }
  &__inner{
    max-width: 69.375em;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    font-weight: 700;
    color: #3E475B;
    text-transform: uppercase;
    max-width: 26em;
    span{
      font-size: 4.125em;
    }
  }
  &__price{
    color: #3E475B;
    display: flex;
    align-items: flex-start;
    font-family: $font2;
    span{
      font-size: 10em;
    }
    svg{
      width: 3.125em;
      height: auto;
      margin: 2.16em 0 0 1.44em;
    }
  }
  &__bottom{
    margin-top: 7.5em;
    position: relative;
    z-index: 2;
  }
}

.collected-info{
  box-shadow: 0 0 25px 0 rgba(0, 0, 0, 0.10);
  background-color: #fff;
  padding: 1.94em;
  &.slideToggle-block--active{
    ._hide{
      opacity: 0;
    }
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    color: #3E475B;
    font-size: 1.5em;
    font-weight: 700;
  }
  &__btn{
    margin-left: 6em;
    flex-shrink: 0;
    border: 2px solid $primary;
    width: 4em;
    height: 4em;
    border-radius: 50%;
    @include center;
    svg{
      width: 1.5em;
      height: auto;
    }
    &:hover{
      background-color: $primary;
      rect{
        fill: #fff;
      }
    }
  }
  .slideToggle-block__wrap{
    padding-top: 1.5em;
  }
}



@media screen and (max-width: 992px){
  .main{
    padding: 2em 1.8em 7.31em;
    &__inner{
      grid-template-columns: 100%;
      gap: 4.16em;
      margin-top: 3.24em;
    }
    &__content{
      padding: 0;
      order: 2;
    }
    &__logo{
      width: 15.9935em;
      margin: 0 auto;
    }
    &__title{
      font-size: 2.5em;
    }
    &__text{
      margin-top: 1.1em;
      p{
        font-size: 1.375em;
      }
    }
    &__img{
      margin: 0;
    }
    &__lines{
      top: -1em;
      width: 100%;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .info{
    padding: 3.88em 1.75em 2.69em;
    grid-template-columns: 100%;
    gap: 3.44em;
    &__stat-title,
    &__text p,
    &__title{
      font-size: 1.375em;
    }
    &__stat{
      margin-top: 1em;
      transform: unset;
      padding-left: 10.86em;
      &-title{
        font-weight: 300;
      }
      &-num{
        font-size: 2.5em;
      }
    }
    &__text{
      margin-top: 1em;
    }
  }

  .info-slider{
    .swiper{
      &-container{
        overflow: unset;
      }
    }
  }

  .stat{
    padding-top: 2.69em;
    padding-bottom: 1.36em;
    &__lines{
      top: -3.5em;
    }
    &__top{
      font-size: .4em;
    }
    &__bottom{
      margin-top: 4em;
    }
  }

  .collected-info{
    padding: .9375em;
    &__title{
      font-size: 1.125em;
      line-height: 126%;
    }
    &__btn{
      width: 2.8125em;
      height: 2.8125em;
      svg{
        width: 1.0625em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .info{
    overflow: unset;
    padding-left: 0;
    padding-right: 0;
    &__lines{
      display: none;
    }
    &__line2{
      position: absolute;
      left: 0;
      top: -5em;
      display: block;
      width: 27.125em;
      height: auto;
    }
    &__content{
      overflow: hidden;
      padding: 0 1.75em;
      position: relative;
    }
    &__line3{
      position: absolute;
      top: 8.23em;
      left: -2.68em;
      width: 33.05831em;
      display: block;
    }
  }

  .info-slider{
    padding-right: 4.15em;
    padding-left: 1.75em;
    overflow: hidden;
    &__bg{
      width: 22.7em;
      top: 2.32em;
    }
  }

  .stat{
    &__top{
      display: block;
      font-size: 1em;
    }
    &__title{
      span{
        font-size: 2.5em;
      }
    }
    &__price{
      color: $primary;
      margin-top: 2.44em;
      padding-left: 3.6em;
      align-items: flex-end;
      svg{
        width: 3.27844em;
        margin: 0 0 -.6em .32em;
      }
      span{
        font-size: 6.46em;
      }
    }
    &__lines{
      display: none;
    }
    &__lines2{
      display: block;
      top: 9em;
    }
  }

  .collected-info{
    padding: 1.38em 1.25em 1.75em;
    position: relative;
    &__btn{
      position: absolute;
      bottom: -1.37em;
      left: 50%;
      transform: translateX(-50%);
      margin: 0;
      background-color: #fff;
    }
    .slideToggle-block__wrap{
      padding-top: 1em;
    }
  }
}
