.member{
  padding: 7em 5.75em 2.31em;
  &__inner{
    border-radius: 6.25em;
    padding: 4.94em;
    background-color: $secondary;
    text-align: center;
  }
  &__title{
    font-size: 1.75em;
    line-height: 142%;
    font-weight: 700;
    text-transform: uppercase;
    color: #3E475B;
    letter-spacing: 0.05em;
  }
  &__nav{
    margin-top: .625em;
    display: flex;
    justify-content: center;
  }
  .m-btn{
    height: 2.875em;
    width: 16em;
    max-width: 100%;
    border-radius: 6.25em;
    span{
      font-size: 1.125em;
      line-height: 142%;
    }
  }
}

.about{
  padding: 2.5em 8em 2.5em 25em;
  display: grid;
  grid-template-columns: calc(100% - 50.5em) 41.5em;
  gap: 9em;
  position: relative;
  &__line,
  &__line2{
    position: absolute;
    top: 4em;
    left: 0;
    width: 100%;
    height: auto;
  }
  &__line2{
    display: none;
  }
  &__img{
    position: relative;
    z-index: 2;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__content{
    padding-top: 2.5em;
  }
  &__title{
    text-transform: uppercase;
    font-size: 3.45em;
    font-weight: 700;
    color: #3E475B;
  }
  &__text{
    margin-top: 2.06em;
  }
  &__text,
  &__text2{
    font-weight: 300;
    p{
      font-size: 1.5em;
      line-height: 142%;
      @include lastM(0 0 2em);
    }
  }
  &__text2{
    text-align: right;
  }
}

.help{
  padding: 3em 1.75em 4.28em;
  position: relative;
  &__logo{
    width: 45.4375em;
    max-width: 100%;
    margin: 0 auto 6.5em;
    display: block;
  }
  &__line,
  &__line2{
    position: absolute;
    right: 0;
    top: -12.75em;
    width: 39em;
    height: auto;
  }
  &__line2{
    display: none;
  }
  &__title{
    text-transform: uppercase;
    font-size: 2.25em;
    color: $primary;
    text-align: center;
  }
}


@media screen and (max-width: 992px){
  .member{
    padding: 3.85em 0 2em;
    position: relative;
    z-index: 2;
    &__inner{
      border-radius: 0;
      padding: 2.1em 1.25em 2.45em;
    }
    &__title{
      font-size: 1.12em;
    }
    &__nav{
      margin-top: 1.46em;
      .m-btn{
        width: 100%;
        border-radius: 0;
        span{
          font-size: .875em;
        }
      }
    }
  }

  .about{
    padding: 2em 1.75em 3.2em;
    grid-template-columns: 100%;
    gap: 2.5em;
    &__img{
      order: -1;
    }
    &__title{
      font-size: 2.5em;
    }
    &__text,
    &__text2{
      p{
        font-size: 1.375em;
      }
    }
    &__text{
      margin-top: 2.5em;
    }
    &__text2{
      margin-top: 3.14em;
    }
  }

  .help{
    overflow: hidden;
    font-size: .75em;
    &__line{
      right: -28em;
    }
  }
}


@media screen and (max-width: 576px){
  .about{
    &__line{
      display: none;
    }
    &__line2{
      display: block;
      top: unset;
      bottom: 0;
    }
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .help{
    font-size: 1em;
    padding: 3.2em 1.75em 3.5em;
    &__line{
      display: none;
    }
    &__line2{
      width: 5.1875em;
      display: block;
      right: 0;
      top: 4.05em;
    }
    &__logo{
      width: 20.6825em;
      margin-bottom: 2.3em;
    }
    &__title{
      font-size: 1.375em;
      line-height: 117%;
    }
  }
}
