.header{
  padding: 2.25em 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__logo,
  &__btn{
    display: none;
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    margin-right: 2.0625em;
    span{
      font-size: .875em;
      line-height: 142%;
    }
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    padding: 0;
    .container{
      padding-right: 0;
    }
    &__inner{
      justify-content: unset;
    }
    &__logo{
      display: block;
      width: 3.875em;
      flex-shrink: 0;
      img{
        width: 100%;
      }
    }
    &__btn{
      display: flex;
      margin-left: auto;
      height: 4.57131em;
      padding: 0 1.875em;
      text-transform: unset;
      font-weight: 300;
      svg{
        width: 1.0625em;
        height: auto;
        flex-shrink: 0;
        margin-right: .875em;
      }
      span{
        font-size: 1em;
        line-height: 120%;
      }
    }
    &._open{
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .menu{
    z-index: 10;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    padding: 5em 1em 2em;
    transition: all .3s;
    background-color: #fff;
    opacity: 0;
    pointer-events: none;
  }

  .nav{
    flex-direction: column;
    &__link{
      margin: 0 0 1.125em;
      span{
        font-size: 1.125em;
      }
    }
  }

  .burger{
    display: flex;
    position: relative;
    z-index: 11;
    width: 1.625em;
    height: 1.0625em;
    margin-right: 1.66em;
    svg{
      @include centerAbsolute;
      width: 100%;
      height: auto;
    }
    ._view2{
      opacity: 0;
    }
  }
}
